.product__container {
  display: flex;
}

.product__info {
  flex: 0 0 35%;
  background-color: #f2f2f2;
  padding: 70px 70px;
}

.product__name {
  margin-bottom: 10px;
  font-weight: 700;
  line-height: 46px;
}

.product__price {
  display: block;
  font-size: 24px;
  margin-bottom: 15px;
}

.product__text {
  margin-bottom: 20px;
}

.product__text:last-child {
  margin-bottom: 40px;
}

.product__form {
  margin-bottom: 50px;
}

.product__colors {
  display: block;
  margin-bottom: 12px;
  font-weight: 700;
  font-size: 18px;
}

.product__link {
  margin-bottom: 20px;
}

.product__slider {
  flex: 0 0 65%;
  padding: 70px;
}

.product-swiper .swiper-pagination-bullet {
  width: 24px;
  height: 6px;
  background-color: #575757;
  opacity: 1;
  border-radius: 0%;
  transition: background-color 0.3s ease;
}

.product-swiper .swiper-pagination-bullet-active {
  width: 24px;
  height: 6px;
  background-color: #000000;
  border-radius: 0%;
}

.product-swiper-pagination {
  bottom: 70px !important;
}

.swiper-slide__wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}
